a {
	color: #fff;
  }
  $easing: cubic-bezier(.6, .05, .28, .91);
  $dullWhite: #f6f6f6;
  
  .header {
	transition: 0.5s all;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 200px;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 70px;
	// transition: 0.2 all ease-in-out;
	z-index: 500;
	background: rgba(170, 170, 170, 0.3);
	color:#fff;
  
	&.nav-up {
	  top: -120px;
	}
  
	.nav {
	  display: flex;
	  margin-right: 0.5rem;
  
	  &__item {
		padding: 0 0.5rem;
	  }
	}
	>div:nth-of-type(2) {
	  display: flex;
	}
  }
  
  #btn9 {
	  
	  top: 21px;
	  
	
	  cursor: pointer;
	  transition: right 500ms $easing;  
	  div {
		  width: 35px;
		  height: 1px;
		  margin-bottom: 8px;
		  background-color: $dullWhite;
		  transition: transform 500ms $easing, opacity 500ms, background-color 250ms;
	  }
	  #bottom9 {
		  width: 25px;
	  }
  }
  
  #btn9.active9 {
	cursor: pointer;
	  div {
		  background-color: $dullWhite;
	  }
	  #top9 {
		  transform: translate(10px,10px) rotate(-135deg);
	  }
	  #middle9 {
		  opacity: 0;
		  transform: rotate(135deg);
	  }
	  #bottom9 {
		  width: 35px;
		  transform: translate(10px,-10px) rotate(-45deg);
	  }
  }
  
  @media screen and (max-width: 1024px) {
	  .header {
		  padding: 0 100px;
	  }
  }
  
  
  @media screen and (max-width: 768px) {
	  .header {
		  padding: 0 20px;
	  }
  }
  
  @media screen and (max-width: 510px) {
	  .header { 
		  a {
			  font-size: 16px;	
		  }
		  
	  }
  }