.classwork {
  position: relative;
  width: 99.99%;
  height: 100vh;

  .workmask {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 200vh;
    overflow: hidden;
    transition: clip-path 1s ease;
   
  }

  .project-work {

    color: #fff;
    width: 75%;

    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    margin: 200vh 0 0 12.5%;

    // sticky
    > div:nth-of-type(1) {
      position: sticky;
      top: 200px;
      > div:nth-of-type(1) {
        background-color: #fff;
        color: #000;
        width: 480px;
        height: 480px;
        border-radius: 20px;
        padding: 50px;
        > div:nth-of-type(1) {
          > h1 {
            font-size: 50px;
            padding-bottom: 10px;
          }
          > p:nth-of-type(1) {
            font-size: 20px;
            padding-bottom: 40px;
            line-height: 30px;
            word-break: keep-all;
          }
          > p:nth-of-type(2) {
            font-size: 16px;
            padding-bottom: 20px;
          }
          > p:nth-of-type(3) {
            font-size: 16px;
            padding-bottom: 40px;
            word-break: keep-all;
          }
          > div:nth-of-type(1) {
            display: flex;
            justify-content: space-between;
            > button:nth-of-type(1) {
              background-color: #883aed;
              color: #fff;
              border-radius: 10px;
              width: 45%;
              height: 60px;
              font-size: 20px;
            }
            > button:nth-of-type(2) {
              background-color: #2b2b2b;
              color: #fff;
              border-radius: 10px;
              width: 45%;
              height: 60px;
              font-size: 20px;
            }
          }
        }
        > div:nth-of-type(2) {
          font-size: 20px;

          .prolist1 {
            display: flex;
            margin-bottom: 15px;
            cursor: pointer;
            .prolist2 {
              margin-right: 15px;

              > img {
                border-radius: 15px;
              }
            }
            .prolist3 {
              > p:nth-last-of-type(2) {
                font-weight: bolder;
              }
            }
          }
        }
      }
      > div:nth-of-type(2) {
        display: flex;
        justify-content: space-around;
        margin: 0 auto;
        width: 100%;

        text-align: center;
        width: 60%;

        font-size: 20px;
        padding-top: 50px;
        

        p {
          cursor: pointer;
        }

        > p:nth-of-type(1)::before {
          content: "<";
          vertical-align: top;
          font-size: 20px;
          font-weight: bold;
          padding-right: 5px;
          cursor: pointer;
        }

        > article {
          background-color: #d9d9d9;
          width: 50px;
          height: 50px;
          border-radius: 25px;
          position: relative;
          top: -10px;

          cursor: pointer;
          > div {
            position: relative;
            top: 16px;
            left: 14px;
            > div:nth-of-type(1),
            div:nth-of-type(2) {
              display: flex;
              flex-direction: row;
              justify-content: space-around;
              width: 45%;
              padding-bottom: 6px;
            }
          }
          span {
            display: inline-block;
            background-color: #000;
            width: 6px;
            height: 6px;
          }
        }

        > p:nth-of-type(2)::after {
          content: ">";
          vertical-align: top;
          font-size: 20px;
          font-weight: bold;
          padding-left: 2px;
          cursor: pointer;
        }
      }
    }
    //sticky옆 p
    > div:nth-of-type(2) {
      text-align: left;
      width: 60%;
      font-size: 20px;
      margin-left: 20px ;
      > p:nth-of-type(2),
      p:nth-of-type(4),
      p:nth-of-type(6) {
        color: rgba(255, 255, 255, 0.7);
        padding-bottom: 80px;
        padding-top: 20px;
        word-break: keep-all;
      }
      > img {
        border-radius: 20px;
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .classwork {
    .project-work {
      flex-direction: column;
      //stickey
      > div:nth-of-type(1) {
        position: absolute;
        top: 0;
        > div:nth-of-type(1) {
          width: 580px;
        }
      }
      > div:nth-of-type(2) {
        position: absolute;
        top: 600px;
      }
    }
  }
  .makbigimg {
    background-image: url('../../public/nextpro/healingline1.png');
    background-size: cover; 
    width: 100vw;
    height: 100vh;
  }
}

@media screen and (max-width: 768px) {
  .classwork {
    .project-work {
      //stickey
      > div:nth-of-type(1) {
        > div:nth-of-type(1) {
          width: 400px;
        }
      }
      > div:nth-of-type(2) {
        > img {
          width: 400px;
        }
      }
    }
  }
}

@media screen and (max-width: 510px) {
  .classwork {
    .project-work {
      > div:nth-of-type(1) {
        > div:nth-of-type(1) {
          width: 370px;
          > div:nth-of-type(1) {
            > div:nth-of-type(1) {
              > button:nth-of-type(1),> button:nth-of-type(2) {
                width:40%;
                height:50px;
                font-size:16px;
              }
              
            }
          }
        }
      }
      > div:nth-of-type(2) {
        > img {
          width: 300px;
        }
      }
    }
  }
}

@media screen and (max-width: 439px) {
  .classwork {
    .project-work {
      > div:nth-of-type(1) {
        margin-left: -45px;
        > div:nth-of-type(1) {
          width: 100%;
          padding:25px;
          height: 350px;
     
          > div:nth-of-type(1) {
            >h1 {
              font-size: 30px;
              padding-bottom: 3px;
            }
            > p:nth-of-type(1) {
              font-size: 12px;
              padding-bottom: 10px;
              line-height: 20px;
            }
            > div:nth-of-type(1) {
              > button:nth-of-type(1),> button:nth-of-type(2) {
                width:30%;
                height:50px;
                font-size:12px;
              }
              
            }
          }
        }
      }
      > div:nth-of-type(2) {
        margin-left: -20px;
      }
    }
  }
}